.session-container {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #2493a7;
}

.session-container h3 {
  text-align: center;
  margin-bottom: 20px;
}

.session-container button {
  display: block;
  width: 100%;
  padding: 10px;
  margin-top: 20px;
  border: none;
  border-radius: 5px;
  background-color: #27d430;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
}

.session-container button:hover {
  background-color: #86bb1c;
}

.user-details {
  text-align: center; /* Center the content */
  margin-top: 30px; /* Adjust margin-top to create space between the container and user details */
}

.detail {
  font-size: 24px; /* Increase font size */
  margin-bottom: 20px; /* Increase margin-bottom for space between details */
}
